"use client"

import { useState, useRef, useEffect } from "react"
import { Button } from "@/components/ui/button"

export default function Home() {
  const [startPoint, setStartPoint] = useState<{x: number, y: number} | null>(null)
  const [endPoint, setEndPoint] = useState<{x: number, y: number} | null>(null)
  const [path, setPath] = useState<Array<{x: number, y: number}>>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const svgRef = useRef<SVGSVGElement>(null)

  const handleSvgClick = async (e: React.MouseEvent<SVGSVGElement>) => {
    if (!svgRef.current) return

    const rect = svgRef.current.getBoundingClientRect()
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top

    if (!startPoint) {
      setStartPoint({ x, y })
      setError(null)
    } else if (!endPoint) {
      setEndPoint({ x, y })
      setLoading(true)
      setError(null)
      
      try {
        const response = await fetch("/api/find-path", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            start: startPoint,
            end: { x, y }
          }),
        })
        
        if (!response.ok) {
          const errorData = await response.json()
          throw new Error(errorData.error || "Failed to find path")
        }
        
        const data = await response.json()
        setPath(data.path)
      } catch (err) {
        setError(err instanceof Error ? err.message : "An error occurred")
      } finally {
        setLoading(false)
      }
    }
  }

  const renderPath = () => {
    if (path.length < 2) return null

    const pathPoints = path.map(point => `${point.x},${point.y}`).join(" ")
    return (
      <polyline
        points={pathPoints}
        fill="none"
        stroke="green"
        strokeWidth="3"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    )
  }

  const resetPoints = () => {
    setStartPoint(null)
    setEndPoint(null)
    setPath([])
    setError(null)
  }

  return (
    <main className="flex min-h-screen flex-col items-center p-8 bg-gray-50">
      <div className="w-full max-w-6xl bg-white rounded-lg shadow-lg p-8">
        <h1 className="text-3xl font-bold mb-8 text-center">Mall Path Finder</h1>
        
        <div className="relative bg-white rounded-lg overflow-hidden border border-gray-200">
          <svg
            ref={svgRef}
            width="1200"
            height="600"
            viewBox="0 0 1200 600"
            onClick={handleSvgClick}
            className="w-full h-auto cursor-pointer"
          >
            <image href="/mall-map.svg" width="1200" height="600" />
            
            {startPoint && (
              <circle
                cx={startPoint.x}
                cy={startPoint.y}
                r="6"
                fill="blue"
                className="animate-pulse"
              />
            )}
            
            {endPoint && (
              <circle
                cx={endPoint.x}
                cy={endPoint.y}
                r="6"
                fill="red"
                className="animate-pulse"
              />
            )}
            
            {renderPath()}
          </svg>
          
          {loading && (
            <div className="absolute inset-0 bg-black bg-opacity-20 flex items-center justify-center">
              <div className="bg-white p-4 rounded-lg shadow-lg flex items-center gap-2">
                <div className="w-4 h-4 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
                <span>Finding shortest path...</span>
              </div>
            </div>
          )}
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center justify-between gap-4 px-4">
          <Button 
            onClick={resetPoints}
            className="w-full sm:w-auto"
            variant="outline"
          >
            Reset Points
          </Button>
          
          <div className="flex flex-wrap justify-center gap-6">
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-blue-500"></div>
              <span className="text-sm">Start Point</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-red-500"></div>
              <span className="text-sm">End Point</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-6 h-1 bg-green-500"></div>
              <span className="text-sm">Shortest Path</span>
            </div>
          </div>
        </div>

        {error && (
          <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg text-red-600 text-center">
            <div className="flex items-center justify-center gap-2">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
              </svg>
              <span>{error}</span>
            </div>
          </div>
        )}
      </div>
    </main>
  )
}
